/* variable */
:root {
  --body-color: #dddddd;
  --primary-color: #8750f7;
  --primary-color-light: #140c1c;
  --secondary-color: #2a1454;
  --white-color: #ffffff;
  --off-white-color: #f6f3fc;
  --black-color: #050709;
  --gray-color: #d9d9d9;
  --gray-color2: #747779;
  --gray-color3: #22272c;
  --accent-color: #0f0715;
  --cream-light-color: #f6f3fc;

  /* font family */
  --body-font: "Sora", sans-serif;
  --russo: "Russo One", sans-serif;
  --font-family: "Font Awesome 6 Pro";
}
/* default css */
* {
  scrollbar-width: thin;
  scrollbar-color: var(--primary-color) var(--secondary-color);
}

::-webkit-scrollbar {
  height: 4px;
  width: 4px;
  background: var(--secondary-color);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  -webkit-border-radius: 1ex;
  -webkit-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.75);
}

::-webkit-scrollbar-corner {
  background: transparent;
}

body {
  color: var(--body-color);
  font-family: var(--body-font);
}
/* typography */
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
  font-weight: 700;
}
p {
  line-height: 1.5;
}
a {
  display: inline-block;
  transition: all 0.3s;
}
img {
  max-width: 100%;
  height: auto;
}

/* container */
.container {
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}
/* header css  */
@-webkit-keyframes bounceInUp {
  0%,
  10%,
  35%,
  50%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  10% {
    -webkit-transform: translate3d(0, 0px, 0);
    transform: translate3d(0, 0px, 0);
  }
  35% {
    -webkit-transform: translate3d(0, -10px, 0);
    transform: translate3d(0, -10px, 0);
  }
  50% {
    -webkit-transform: translate3d(0, 2px, 0);
    transform: translate3d(0, 2px, 0);
  }
  80% {
    -webkit-transform: translate3d(0, -90px, 0);
    transform: translate3d(0, -90px, 0);
  }
  to {
    -webkit-transform: translate3d(0, -3000px, 0);
    transform: translate3d(0, -3000px, 0);
  }
}
@keyframes bounceInUp {
  0%,
  10%,
  35%,
  50%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  10% {
    -webkit-transform: translate3d(0, 0px, 0);
    transform: translate3d(0, 0px, 0);
  }
  35% {
    -webkit-transform: translate3d(0, -10px, 0);
    transform: translate3d(0, -10px, 0);
  }
  50% {
    -webkit-transform: translate3d(0, 2px, 0);
    transform: translate3d(0, 2px, 0);
  }
  80% {
    -webkit-transform: translate3d(0, -90px, 0);
    transform: translate3d(0, -90px, 0);
  }
  to {
    -webkit-transform: translate3d(0, -3000px, 0);
    transform: translate3d(0, -3000px, 0);
  }
}
.header-area {
  position: relative;
  z-index: 9;
  background-color: var(--off-white-color);
}
.dark .header-area {
  background-color: var(--black-color);
}
.header-area.header-absolute {
  position: absolute;
  background-color: transparent;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 99;
}

.header-area.header-2 {
  width: 100%;
  position: fixed;
  top: -90px;
  left: 0;
  z-index: 100;
  opacity: 0;
  transition: 0.3s;
  box-shadow: 0 0 30px rgba(135, 80, 247, 0.2);
}
.header-area.header-2.sticky {
  top: 0;
  opacity: 1;
  animation: bounceInDown 1s;
  animation-duration: 1s;
  animation-fill-mode: both;
}
.header-area.header-2.sticky-out {
  top: 0;
  opacity: 1;
  animation: bounceInUp 1s;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}
.nav .nav_item .active:after {
  width: 100%;
}
.nav .nav_item ul li .active {
  color: var(--primary-color);
}

.menu-bar button {
  border: none;
  padding: 0;
  background-color: transparent;
  height: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  transform-origin: center center;
  transform: rotate(0deg);
  cursor: pointer;
  transition: transform 300ms ease;
}
.menu-bar button span {
  height: 3px;
  width: 35px;
  display: block;
  background: var(--primary-color);
  cursor: pointer;
  transition: all 0.3s ease-in-out 0s;
  margin-left: auto;
}
.dark .menu-bar button span {
  background: var(--white-color);
}
.menu-bar button span:nth-child(2) {
  width: 40px;
  transition-delay: 200ms;
}
.menu-bar button span:nth-child(3) {
  width: 30px;
}
.menu-bar button span:nth-child(4) {
  position: absolute;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 3px;
  height: 0;
  transition: height 400ms;
}
.menu-bar button.active {
  transform: rotate(45deg);
  transition-delay: 400ms;
}
.menu-bar button.active span:nth-child(1) {
  width: 0;
}
.menu-bar button.active span:nth-child(3) {
  width: 0;
}
.menu-bar button.active span:nth-child(4) {
  height: 40px;
  transition: height 200ms ease;
  transition-delay: 200ms;
}
/* mobile menu */
.mobile-menu {
  transform: scaleY(0);
  transition: all 0.5s;
}
.mobile-menu.active {
  transform: scaleY(1);
